}

.modal-city {
  display: flex;
  align-items: center;
  color: var(--main-color-light);
}
@media (any-hover: hover) {
  .modal-city:hover {
    color: var(--main-color-dark);
  }
}
.modal-city__btn {
  display: block;
  text-transform: uppercase;
  padding: 10px 0 10px 10px;
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
