.main-slider__box {
  height: calc(100vh - 165px);
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: contain;
  position: relative;
}
@media only screen and (max-width: 1229px) {
  .main-slider__box {
    height: calc(100vh - 140px);
  }
}
@media only screen and (max-width: 991px) {
  .main-slider__box {
    background-size: 450px auto;
  }
}
@media only screen and (max-width: 767px) {
  .main-slider__box {
    background-image: none !important;
    height: 100%;
  }
}
.main-slider__wrapper-content {
  padding-top: 150px;
  display: flex;
  flex-direction: column;
  height: 605px;
}
@supports (padding-left: clamp( 1.875rem , -1.8921232877rem  +  18.8356164384vw , 19.0625rem )) {
  .main-slider__wrapper-content {
    padding-left: clamp( 1.875rem , -1.8921232877rem  +  18.8356164384vw , 19.0625rem );
  }
}
@supports not (padding-left: clamp( 1.875rem , -1.8921232877rem  +  18.8356164384vw , 19.0625rem )) {
  .main-slider__wrapper-content {
    padding-left: calc(
			1.875rem + 17.1875 * (100vw - 20rem) / 91.25
		);
  }
}
@media only screen and (max-width: 991px) {
  .main-slider__wrapper-content {
    padding-top: 100px;
    height: 705px;
  }
}
@media only screen and (max-width: 767px) {
  .main-slider__wrapper-content {
    height: 360px;
    padding-bottom: 30px;
  }
}
@media only screen and (max-width: 559px) {
